body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-playfair-display: "Playfair Display";
  --font-lato: Lato;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gray-100: #1c1917;
  --color-red-100: #f50000;
  --color-gainsboro: #e7e5e4;
  --color-darkgray: #a8a29e;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-13xl: 32px;
  --gap-xl: 20px;
  --gap-base: 16px;

  /* Paddings */
  --padding-45xl: 64px;
  --padding-xl: 20px;
  --padding-mini: 15px;
  --padding-base: 16px;

  /* Border radiuses */
  --br-81xl: 100px;
  --br-13xl: 32px;
}
